.alinhar{
   background:#efefef;
   min-height: 50vh;
   width: 100vw;
   display: flex;
   justify-content:center;
   align-items: center;
}

#linha{
   
   box-sizing: border-box;
   margin: 0;
   padding: 0;
}


.carousel-container{
   width: 100%;
   max-width: 1200px;
   height: 100%;
   background:silver;
   display: flex;
   position: relative;
}

.carousel{
   
   display: flex;
   height: 100%;
   width: 100%;
   overflow-x: scroll;
   scroll-snap-type: x mandatory;
   scroll-behavior: smooth;
}

.carousel-item{
   
   display: flex;
   flex-direction: column;
   flex: 1 0 100%;
   scroll-snap-align: start;
}

.carousel-item img{
   height: 100%;
   width: 100%;
   object-fit: cover;
   
}

.carousel-item.active p{
   opacity: 1;
   transform: scale(1.1) translateY(-40px);
   
}

.carousel-item p{
   position: absolute;
   transition: all 0.5s ease-in-out;
   opacity: 0;
   inset: auto 0 0 0;
   padding-block: 1rem;
   text-align: center;
   color: #efefef;
   font-size: 2rem;
}

.carousel-btn{
   height: 30px;
   width: 30px;
   position: absolute;
   border: 2px solid white;
   transform: rotate(45deg);
   z-index: 2;
   top: 50%;
   cursor: pointer;
   display: none;
}

@media(width > 600px){
   .carousel-btn{display: inline-block;}
}

.carousel-item.active p{
   opacity: 1;
   transform: scale(1.1) translateY(-40px);
   
}

.carousel-item p{
   
   opacity: 0;
}

.left-btn{
   border-bottom: 6px  solid #08629a;
   border-left: 6px solid #08629a;
   left: 0;
}

.right-btn{
   border-right: 6px solid #08629a ;
   border-top: 6px solid #08629a;
   right: 0;
}


::-webkit-scrollbar{
   height: 0;
   width: 0;
   
}

